import React from 'react';
import './Modal.css'

const Modal = ({active, children, style, className}) => {
    return active && (
        <div className='modal-wrapper'>
            <div
                style={style}
                className={className ? 'modal ' + className: 'modal'}
            >
                {children}
            </div>
        </div>
    )
};

export default Modal;


