import {combineReducers} from 'redux';

const ui = (
    state = {
        isLoading: false,
        currentFile: null
    },
    action
) => {
    switch (action.type) {
        case 'REQUEST_FILE_LIST':
        case 'REQUEST_FILE':
            return {
                ...state,
                isLoading: true
            };
        case 'RECEIVE_FILE_LIST':
            return {
                ...state,
                isLoading: false
            };
        case 'RECEIVE_FILE':
            return {
                ...state,
                currentFile: action.fileId,
                isLoading: false
            };
        default:
            return state;
    }
};

const fileList = (
    state = [],
    action
) => {
    switch (action.type) {
        case 'RECEIVE_FILE_LIST':
            return action.fileList;
        case 'RECEIVE_FILE':
            return state.map(file => {
                return action.file.id !== file.file.id ? file : action.file
            });
        default:
            return state;
    }
};

const rootReducer = combineReducers({ui, fileList});

export default rootReducer;


