import React, { Component } from 'react';
import Modal from '../Modal/Modal';
import { GoogleLogin } from 'react-google-login';
import Button from '../Button/Button'
import './GoogleAuthModal.css'

export default class GoogleAuthModal extends Component {
    constructor (props) {
        super(props);
        this.state = {
            active: true
        }
    }

    responseGoogle(response) {
        console.log(response);
    }

    render () {
        return <Modal
            className='google-auth-modal'
            active={this.state.active}
        >
            <h2>Sign in</h2>
            <GoogleLogin
                responseType='id_token'
                clientId="880362213330-1809l1nnn6lfor04feb1ju0rfdt48egl.apps.googleusercontent.com"
                buttonText="Sign in with Google"
                onSuccess={(r) => this.responseGoogle(r)}
                onFailure={(r) => this.responseGoogle(r)}
                isSignedIn={true}
                className='google-login-button'
            />
            <br />
            <Button
                onClick={() => this.setState({active: false})}
            >Continue without signing in</Button>
        </Modal>
    }
}


