import React, { Component } from 'react';
import './Button.css'

export default class Button extends Component{
    constructor(props) {
        super(props);
        this.state = {
            isRipple: false,
            x: null,
            y: null
        }
    }

    componentWillUnmount() {
        clearTimeout(this.cancelTimeout);
    }

    ripple(e) {
        this.setState({
            isRipple: true,
            x: e.pageX-e.target.getBoundingClientRect().left,
            y: e.pageY-e.target.getBoundingClientRect().top
        });
        this.cancelTimeout = setTimeout(() => {
            this.setState({
                isRipple: false
            });
            this.props.onClick();
        }, 2000);
    }

    render() {
        const { onClick, children, style } = this.props;
        return (
            <button
                className='button'
                onMouseUp={() => {
                    this.setState({isRipple: false});
                    onClick()}
                }
                onMouseDown={(e) => this.ripple(e)}
                style={style}
            >{children}
                {
                    this.state.isRipple &&
                        <div
                            className='ripple-effect'
                            style={{
                                left: this.state.x,
                                right: this.state.y
                            }}
                        />
                }
            </button>
        );
    }
}


